.weather_panel__result{display:grid;grid-template-columns:1fr .4fr;grid-template-rows:minmax(150px, auto)}.weather_panel__location{text-align:center;background:rgba(0,0,0,.4);font-size:20px;padding:6px;border-start-end-radius:20px;border-start-start-radius:20px}.weather_panel__weather{display:grid;grid-template-columns:2fr 1fr 2fr;align-items:center;justify-items:start}.weather_panel__temp{font-size:70px;font-weight:bold;justify-self:end}.weather_panel__temp::after{content:"°";font-weight:bold}.weather_panel__img{width:50px;justify-self:center}.weather_panel__img>img{width:70px;height:70px}.weather_panel__desc{font-size:20px;font-weight:bold}.weather_panel__detailed{font-size:16px;display:grid;align-items:center;align-content:space-evenly}.weather_panel__feelslike::after{content:"°";font-weight:500}.weather_panel__forecast{display:grid;grid-auto-flow:column;justify-content:space-around;align-content:center;align-items:center}.weather_panel__forecast>button{width:30px;height:30px;border-radius:30px;border:none;cursor:pointer;font-size:15px;font-weight:bolder}.weather_panel__forecast>button:hover{opacity:.7}.weather_panel__item{display:grid;justify-items:center;font-size:20px;font-weight:bold}.weather_panel__item>.weather_panel__item__img{width:30px;height:30px}.weather_panel__item>.weather_panel__item__img>img{width:35px;height:35px}.weather_panel__item__temp::after{content:"°";font-weight:500}